$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Alert Box
.wp-block-abb-alert-box {
	transition: opacity .3s ease-in-out, transform 8s ease-in-out;
	transform: translateY(0);
	opacity: 1;

	* {
		box-sizing: border-box;
	}

	.abbAlertBox {
		.alert {
			position: relative;
			display: inline-block;
			max-width: 100%;

			.alertIcon {
				margin-right: 10px;
				font-style: inherit;
			}

			.alertClose {
				font-style: inherit;
				font-size: 18px;
				position: absolute;
				top: 3px;
				right: 4px;
				cursor: pointer;
			}

			.alertMessage,
			.alertMessage * {
				display: inline;
			}
		}
	}

	&.transformHidden {
		transform: translateY(-9999px);
		opacity: 0;
	}
}